body {
  letter-spacing: normal;
  text-rendering: optimizeLegibility;
  font-family: "Spectral", serif;
  font-weight: 400;
  font-size: 16px;
  font-style: normal;
  background-color: #fffbfc;
  color: rgba(0, 0, 0, 0.9);
  vertical-align: baseline; }

.page {
  display: block;
  min-width: 320px;
  background-color: #fffbfc;
  margin: 0 auto; }

ul {
  margin: 0;
  padding: 0; }

li {
  margin: 0;
  padding: 0;
  list-style: none; }

.side-intro__wrapper {
  position: relative;
  padding-bottom: 6em;
  padding-top: 8em;
  padding-left: 1.5em;
  padding-right: 1.5em;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2); }

.side-intro__name {
  margin: 0;
  font-size: 3em;
  font-weight: 800;
  letter-spacing: -0.03em;
  margin-bottom: 0.3em; }

.side-intro__position {
  margin: 0;
  font-weight: 300;
  font-size: 1.2em;
  margin-bottom: 3em; }

.side-intro__contact-list {
  position: absolute;
  top: 30px;
  display: block;
  font-family: "Spectral script=all rev=1";
  font-style: italic;
  font-size: 1em; }

.side-intro__contact-link--linkedin {
  margin-right: 15px; }

.main-nav__list {
  font-size: 1.2em;
  font-weight: 300; }

.main-nav__item {
  margin-bottom: 0.5em; }
  .main-nav__item:last-child {
    margin-bottom: 0; }

.main-nav__link {
  text-decoration: none;
  color: rgba(0, 0, 0, 0.9); }

.wrapper {
  padding-left: 1.5em;
  padding-right: 1.5em;
  padding-top: 2em;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2); }

.experience__title {
  margin: 0;
  margin-bottom: 2em;
  font-weight: 400;
  font-size: 1.2em; }

.experience__item {
  padding-top: 2em;
  padding-bottom: 2em;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2); }
  .experience__item:first-child {
    padding-top: 0; }
  .experience__item:last-child {
    border-bottom: none; }

.experience__number {
  margin: 0;
  margin-bottom: 0.5em; }

@media (min-width: 768px) {
  .experience__item {
    display: flex;
    padding-bottom: 2em; }
  .experience__table-side {
    padding-right: 1em;
    width: 30%; }
  .experience__table-main {
    width: 70%; } }

.table-side {
  margin-bottom: 2em;
  font-family: "Spectral script=all rev=1";
  font-style: italic; }
  .table-side__place {
    margin: 0;
    margin-bottom: 0.2em;
    font-size: 1.2em;
    font-weight: 600;
    font-feature-settings: normal;
    font-kerning: normal; }
  .table-side__city {
    font-weight: 100;
    font-size: 1em;
    margin: 0;
    font-feature-settings: normal;
    font-kerning: normal; }
  .table-side__time {
    font-weight: 100;
    font-size: 1em;
    margin: 0;
    margin-bottom: 0.2em;
    font-feature-settings: normal;
    font-kerning: normal; }

@media (min-width: 768px) {
  .table-side {
    margin-bottom: 0; } }

.table-main__title {
  font-size: 1.2em;
  font-weight: 600;
  margin: 0;
  margin-bottom: 0.5em;
  max-width: 580px; }

.table-main__content {
  margin: 0;
  font-size: 1em;
  max-width: 540px;
  margin-bottom: 0.5em; }

@media (min-width: 768px) {
  .table-main__title {
    max-width: 100%; } }

.various-list__item {
  margin-bottom: 2em; }
  .various-list__item:last-child {
    margin-bottom: 0; }

.additional__wrapper {
  padding-left: 1.5em;
  padding-right: 1.5em;
  padding-top: 4em;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2); }

.additional__item {
  padding-top: 2em;
  padding-bottom: 2em;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2); }
  .additional__item:first-child {
    padding-top: 0; }
  .additional__item:last-child {
    border-bottom: none; }

.additional__number {
  margin: 0;
  margin-bottom: 0.5em; }

.additional__title {
  margin: 0;
  margin-bottom: 3em;
  font-weight: 400;
  font-size: 1.5em; }

@media (min-width: 768px) {
  .additional__item {
    display: flex;
    padding-bottom: 2em; }
  .additional__table-side {
    padding-right: 1em;
    width: 30%; }
  .additional__table-main {
    width: 70%; } }
